    opacity: 1;
    visibility: visible;
  }

  body nav.closing {
    transform: translateX(-100%);
  } 
  .content {
    padding: 1rem;
  }
  .todos {
    padding: 1rem 0;
  }
  .sort-button svg {
    display: none;
  }
  .text {
    width: 100%;
    max-width: 20rem;
