  .initial {
    padding: 0;
    width: 100%;
  }

  .todo .right {
    margin-left: 0;
  }
  .todo .left {
    flex-direction: column;
    align-items: stretch;
    gap: 0.5rem;
  }
  .buttons {
    gap: 1rem;
  }
  .initial {
    position: relative;
  }
  .checkbox {
    position: absolute;
    top: 0;
    right: 0;
  }
